<template>
  <div>
    <Loadings v-if="list == null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <div class="setting" @click="openSetting">
        <van-icon color="gray" size="20" name="setting-o" />
      </div>
      <section class="query">
        <Empty v-if="list.length == 0"></Empty>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          class="list"
          v-else
        >
          <router-link
            :to="{ path: '/InvoiceDetails/' + item.oid }"
            class="item"
            v-for="(item, index) in list"
            :key="index"
          >
            <div class="flex-sb">
              <div class="info">
                
                <div class="flex-sb">
                  <div>订单号: {{ item.order_no }}</div>
                  <div
                    style="
                      text-align: right;
                      font-size: 12px;
                      color: #333;
                      position: relative;
                      right: -30px;
                    "
                  >
                    {{ item.status == 0 ? "未完成" : "已完成" }}
                  </div>
                </div>
                <div>客户: {{ item.user_name }}</div>
                <div class="flex-sa">
                  <div>材质名称: {{ item.name }}</div>
                  <div>材质: {{ item.material }}</div>
                </div>
                <div class="flex-sa">
                  <div>数量: {{ item.number }}</div>
                  <div>尺寸长: {{ item.length }}</div>
                  <div>尺寸宽: {{ item.width }}</div>
                </div>
                <div>备注: {{ item.remark }}</div>

                <div>日期: {{ formatDate(item.add_time) }}</div>
              </div>
              <van-icon name="arrow" color="#ccc" />
            </div>
          </router-link>
        </van-list>
      </section>

      <!--setting-->
    </div>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
import { getPorder, getPorderDetail } from "@api/public";
import { Icon, Search, ActionSheet, Cell, CellGroup } from "vant";
import Empty from "@components/Empty";
import localStorage from "@utils/store/localStorage";
import Loadings from "@components/Loadings";
export default {
  name: "DataCenter",
  components: {
    [Search.name]: Search,
    [Icon.name]: Icon,
    Loadings,
    Empty,
    [ActionSheet.name]: ActionSheet,
  },
  props: {},
  data: function () {
    return {
      list: null,
      show: false,
      actions: [
        { name: "修改密码" },
        { name: "系统刷新" },
        { name: "退出登录" },
      ],
    };
  },
  created() {
    getPorder().then((res) => {
      this.list = res.data.list;
    });
    //   getPorderDetail({id:'73'}).then((res) => {
    // //    this.list = res.data.list;
    //   });
  },
  mounted: function () {},
  methods: {
    onSelect(e) {
      if (e.name == "修改密码") {
        this.show = false;
        this.$router.replace({ path: "/change_password" });
      } else if (e.name == "系统刷新") {
        this.show = false;
        localStorage.remove("ntoken");
        this.$dialog.message("清除成功");
      } else {
        this.show = false;
        localStorage.remove("ntoken");
        this.$router.replace({ path: "/" });
      }
    },
    openSetting() {
      this.show = true;
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
  },
};
</script>
<style scoped>
.sales-data {
  background: #092e48;
  padding: 20px 0;
}

.sales-data .caption {
  color: #fff;

  padding-left: 19px;
  margin-bottom: 10px;
  font-size: 16px;
}
.setting {
  position: fixed;
  top: 17px;
  left: 10px;
  z-index: 6666666666666666;
  color: red;
}
.van-search--show-action {
  padding-left: 40px;
}
.sales-data .flex-sa {
  height: 110px;
  border-radius: 5px;
  background: #fff;
  margin: 0 9px;

  box-shadow: 0 0 5px #ccc;
  text-align: center;
}
.container {
  padding-bottom: 60px;
}
.sales-data .flex-sa .title {
  color: #999;
  font-size: 12px;
  margin-top: 5px;
}

.to-do,
.query {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
}

.to-do .caption,
.query .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #666;
}

.to-do .list {
  text-align: center;
  padding: 15px 0;
}

.to-do .list .div {
  display: inline-block;
  padding: 0 5px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  margin: 0 3px;
  min-width: 27%;
  color: #666;
  background-color: #f9f9f9;
  font-size: 12px;
  position: relative;
}
.to-do .list .div .fixed {
  position: absolute;
  top: -8px;
  right: -5px;
  line-height: 17px;
  min-width: 20px;
  padding: 0 2px;
  height: 15px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 0 8px #ccc;
  background: #ed4e4e;
  color: #fff;
  font-size: 10px;
}

.to-do .list .div img {
  width: 20px;
  vertical-align: sub;
}

.query {
  padding-bottom: 20px;
}

.query .list {
  margin-bottom: 5px;
}

.query .list .item {
  text-align: center;
  font-size: 14px;
  padding: 10px;
  display: block;
  text-align: left;
  color: #666;
  line-height: 22px;
  border-bottom: 1px solid #eee;
}
.query .list .item .info {
  width: calc(100% - 30px);
}
.query .list .item .info div {
  width: 100%;
}

.value {
  color: #000;
  font-size: 22px;
}

.value span {
  font-size: 12px;
  color: #999;
}

.header-top {
  text-align: center;
  background: #131418;
  color: #fff;
  padding: 10px 0;
}

.header-bottom {
  overflow: hidden;
}

.header-bottom div {
  float: left;
  color: #8c8d91;
  font-size: 14px;
  width: 50%;
  text-align: center;
}

.header-bottom div:first-child {
  border-right: 1px dashed #8c8d91;
}

.header-bottom div span {
  color: #fff;
}

.header-top .center {
  padding: 35px 0;
}

.header-top .center div {
  font-size: 36px;
  font-weight: bold;
  color: #d2ac5b;
}

.header-top .center p {
  color: #8c8d91;
  margin-bottom: 10px;
}

.header-top .caption-name {
  color: #8c8d91;
  font-size: 18px;
}
</style>
